h1, h2, h3 {
  margin-top: 0;
}

a {
  display: block;
}

p a {
  display: inline;
}

li {
  list-style-type: none;
}

h1, h2, h3, h4, h5, h6,
p, span, a, strong, blockquote,
i, b, u, em {
  font-size: 1em;
  font-weight: inherit;
  font-style: inherit;
  text-decoration: none;
  color: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation: none;
    transition: none;
  }
}
*, *::before, *::after {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  vertical-align: baseline;
}

img,
picture,
video,
iframe,
figure {
  max-width: 100%;
  width: 100%;
  display: block;
  object-fit: cover;
  object-position: center center;
}

html {
  scroll-behavior: smooth;
}

table, tr, td {
  border-collapse: collapse;
  border-spacing: 0;
}

svg {
  width: 100%;
  display: block;
  fill: currentColor;
}

body {
  max-width: 100%;
  min-height: 100vh;
  font-size: 100%;
  font-family: var(--font-family);
  color: var(--color-text);
  background: var(--bg);
  line-height: 1.4em;
  font-smooth: always;
  --webkit-font-smoothing: antialiased;
  --moz-osx-font-smoothing: grayscale;
  transition: 0.5s ease-in-out;
  position: relative;
}

.artistic-text {
  font-family: var(--font-family-2);
  background: linear-gradient(var(--main-color) 35%, var(--bg) 88%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.artistic-text--responsive {
  font-size: 16vw;
  line-height: 100%;
}
@media (min-width: 40em) {
  .artistic-text--responsive {
    font-size: 5rem;
  }
}
@media (min-width: 69.6875em) {
  .artistic-text--responsive {
    font-size: 7.5rem;
  }
}

.main-container {
  min-width: 23.4375rem;
  max-width: 87.5rem;
  padding: 0;
  margin: 0 auto;
}
@media (min-width: 69.6875em) {
  .main-container {
    min-width: 69.6875rem;
  }
}

.container {
  min-width: 23.4375rem;
  margin: 0 auto 2.8125rem;
}
@media (min-width: 69.6875em) {
  .container {
    padding: 0 4rem;
  }
}

.attribution {
  position: fixed;
  padding: 0 0 0 1.5rem;
  background: var(--card-hover);
  border-radius: 50px;
  bottom: 10px;
  right: 10px;
  width: 4rem;
  height: 4rem;
  overflow: hidden;
  transition: 0.5s ease-in-out;
  text-align: left;
  line-height: 1.2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.9rem;
}
.attribution__text {
  transition: 0.3s 0.3s;
  opacity: 0;
  min-width: 19rem;
  margin: 0.2rem 0 0;
  color: var(--White);
}
.attribution__text--a {
  color: var(--title-color2);
}
.attribution__img {
  position: absolute;
  bottom: 0;
  right: 0;
  max-width: 4rem;
  border: 4px solid var(--main-color);
  border-radius: 50%;
  cursor: pointer;
  transition: 0.5s ease-in-out;
}
.attribution:hover {
  width: 19.5rem;
  border-radius: 50px;
  color: var(--main-color);
}
.attribution:hover .attribution__text {
  opacity: 1;
}

.navbar {
  display: none;
}

.modal-navbar {
  display: none;
}

.header {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  gap: 0;
  height: 4.25rem;
  padding: 0 1.375rem;
}
@media (min-width: 69.6875em) {
  .header {
    height: 7.1875rem;
    border-bottom: 1px solid var(--bg-color2);
  }
}
.header__navigation {
  width: 10.9375rem;
  height: 100%;
  border: 1px solid red;
  transition: 0.3s ease-in-out;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  gap: 0.5rem;
}
@media (min-width: 69.6875em) {
  .header__navigation {
    width: 37.5rem;
  }
}
.header__menu {
  width: 1rem;
}
.header__menu:hover {
  cursor: pointer;
}
@media (min-width: 69.6875em) {
  .header__menu {
    display: none;
  }
}
.header__logo {
  width: min-content;
}
.header__cart-avatar {
  width: 4.375rem;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  gap: 1.4rem;
  transition: 0.3s ease-in-out;
}
@media (min-width: 69.6875em) {
  .header__cart-avatar {
    width: 7.5rem;
  }
}
.header__cart {
  position: relative;
}
.header__cart--notification {
  display: none;
  width: 1rem;
  height: 0.8rem;
  color: var(--bg-color);
  font-size: 0.7rem;
  text-align: center;
  line-height: 0.8rem;
  border-radius: 1rem;
  position: absolute;
  top: -0.1875rem;
  right: -0.3125rem;
  background-color: var(--main-color);
}
.header__cart:hover {
  cursor: pointer;
}
.header__avatar {
  width: 1.625rem;
}
.header__avatar:hover {
  cursor: pointer;
}
@media (min-width: 69.6875em) {
  .header__avatar {
    width: 3rem;
  }
  .header__avatar:hover {
    border: 2px solid var(--main-color);
    border-radius: 50%;
  }
}